import pic from "./images/profile pic.jpg";
import {FaHeart, FaFacebook} from "react-icons/fa";
import {useState} from 'react';

function App() {

  const [text, setText] = useState ('I am a Frontend Developer');
  const [count, setCount] = useState (0);

  return (
    <div className="App">
      <div className="wrapper">
        <div className="card">
          <section className="pic">
           <img src={pic} alt="profile picture"/>
          </section>
         <section section className="main">
            <h1>Hi!</h1>
           <p className="name">{text}</p>
           <button className="button1" onClick={ () => {
             setText('My name is Isidro Hernandez')
           }}>Name</button> 
           <button onClick={ () => {
             setText('I am a Frontend Developer')
           }}>Skills</button>
           <section className="footer">
             <span className="number">{count}</span><span className="heart" onClick={ () => {setCount(count + 1)}}><FaHeart/></span>
           </section>
           <a href='https://www.facebook.com/isidro.p.hernandez' target='#'><FaFacebook/></a>
        </section>
        </div>
      </div>
    </div>
  );
}

export default App;
